.parent {
    position: relative;
  }

.child {
    position: absolute;
    left: 10%;
    top: 50%;
    -webkit-transform: translate(-10%, -50%);
    -moz-transform: translate(-10%, -50%);
    -ms-transform: translate(-10%, -50%);
    -o-transform: translate(-10%, -50%);
    transform: translate(-10%, -50%);
  }
  